.App {
  text-align: center;
  margin: 0;
  @media (max-width: 620px) {
  }
}
body {
  overflow: hidden;
  background-color: rgb(0, 0, 0);
  filter: brightness(0.8);
}
.appMain {
  position: relative;
  margin: 0 auto;
  width: 100vw;
  height: 99vh;
  z-index: 1;
  filter: brightness(0.8);
  @media (max-width: 620px) {
    object-fit: cover;
    object-position: 31%;
  }
  /* mix-blend-mode: color-burn;
  filter: brightness(2);
  z-index: 2;
 */
}

.witch {
  position: absolute;
  left: 94%;
  top: 24%;
  width: 8%;
  z-index: 3;
  mix-blend-mode: color-burn;
  filter: brightness(2);
  animation: moveWitch infinite ease-out 2.5s;
  @media (max-width: 620px) {
    left: 95%;
  }
}
@keyframes moveWitch {
  0% {
    transform: scale(0.3);
  }
  25% {
    top: 12%;
    transform: scale(1.2);
  }
  50% {
    left: -0rem;
    top: 14%;
    transform: scaleX(0.8);
  }
  75% {
    top: 7%;
    transform: scale(0.4);
  }

  97% {
    top: 10%;
    opacity: 0.3;
  }
  100% {
    opacity: 0;
    left: -5%;
    transform: scale(0.2);
  }
}

.lapid {
  width: 45%;
  height: 45%;
  display: block;
  position: absolute;
  z-index: 3;
  left: 32%;
  bottom: 2%;
  filter: brightness(1.5);
  filter: blur(1px);
  filter: brightness(0.2);
  transform: rotate(3deg);
  animation: moveLapid 1 ease 3.5s;
  @media (max-width: 620px) {
    width: 67%;
    left: 12%;
    height: 35%;
    scale: 1.5;
  }
}

.textLapid {
  font-size: 14.5px;
  bottom: 10%;
  left: 45.5%;
  position: absolute;
  display: block;
  transform: rotate(3deg);
  color: aliceblue;
  width: 20%;
  z-index: 5;
  padding: 0;
  animation: moveLapid 1 ease 3.5s;
  color: rgba(255, 255, 255, 0.813);
  text-shadow: 1px 1px 1px rgb(255, 7, 7);
  font-weight: 100;
  line-height: 1.2;
  @media (max-width: 620px) {
    width: 50%;
    left: 22%;
  }
}

@keyframes moveLapid {
  0% {
    bottom: -44rem;
  }
  25% {
    transform: rotate(-7deg);
  }
  50% {
    transform: rotate(14deg);
  }

  75% {
    transform: rotate(-3deg);
  }
  100% {
  }
}
.candle1 {
  mix-blend-mode: difference;
  filter: brightness(0.9);
  z-index: 1;
  position: absolute;
  left: 5%;
  bottom: 2%;
  height: 12rem;
  @media (max-width: 620px) {
    left: 1%;
    width: 4rem;
  }
}
.candle2 {
  mix-blend-mode: difference;
  filter: brightness(0.9);
  z-index: 1;
  position: absolute;
  right: 5%;
  bottom: 2%;
  height: 12rem;
  @media (max-width: 620px) {
    right: 1%;
    width: 4rem;
  }
}
